angular.module('fitforlife', ['ngRoute', 'ngAnimate', 'ngSanitize', 'ngTouch', 'ui.bootstrap', 'ngMessages', 'angular-ladda', 'ui-notification', 'ngStorage']);

function config($routeProvider, $locationProvider, NotificationProvider) {
  NotificationProvider.setOptions({
    delay: 2000,
    startTop: 20,
    startRight: 10,
    verticalSpacing: 20,
    horizontalSpacing: 20,
    positionX: 'right',
    positionY: 'bottom'
  });

  $routeProvider
    .when('/register', {
      templateUrl: 'views/_auth/register.view.html',
      controller: 'registerCtrl',
      controllerAs: 'vm'
    }).when('/login', {
      templateUrl: 'views/_auth/login.view.html',
      controller: 'loginCtrl',
      controllerAs: 'vm'
    }).when('/home', {
      templateUrl: 'views/_home/home.view.html',
      controller: 'homeCtrl',
      controllerAs: 'vm'
    }).when('/feeds', {
      templateUrl: 'views/_feeds/feeds.view.html',
      controller: 'feedsCtrl',
      controllerAs: 'vm'
    }).when('/profile', {
      templateUrl: 'views/_profile/profile.view.html',
      controller: 'profileCtrl',
      controllerAs: 'vm'
    })
    .otherwise({
      redirectTo: '/home'
    });

  // use the HTML5 history API
  $locationProvider.html5Mode(true);
}

function run($rootScope, $location, $timeout, authService) {
  $rootScope.$on('$routeChangeStart', function(event, nextRoute, currentRoute) {
    console.log("redirect to: " + $location.path());
    if (authService.isLoggedIn()) { // check if user is logged in
      // user is logged in
    } else {
      if (($location.path() != '/login' && $location.path() != '/register' && $location.path() != '/home')) {
        $location.path('/home');
      }
    }
  });
}

angular.module('fitforlife').config(['$routeProvider', '$locationProvider', 'NotificationProvider', config])
  .run(['$rootScope', '$location', '$timeout', 'authService', run]);




$.validator.setDefaults({
  highlight: function(element) {
    $(element).closest('.form-group').removeClass('has-success').addClass('has-error')
      // $(element).parent().find('.form-control-feedback').removeClass('fa-check').addClass('fa-remove');
  },
  unhighlight: function(element) {
    $(element).closest('.form-group').removeClass('has-error').addClass('has-success');
    // $(element).parent().find('.form-control-feedback').removeClass('fa-remove').addClass('fa-check');
  },
  errorClass: 'error',
  errorPlacement: function(error, element) {
    if (element.parent('.input-group').length) {
      error.insertAfter(element.parent());
    } else {
      error.insertAfter($(element).parent().find('.form-control-feedback'));
    }
  }
});

(function() {

  angular
    .module('fitforlife')
    .service('authService', authService);

  authService.$inject = ['$http', '$window', '$localStorage'];

  function authService($http, $window, $localStorage) {

    var register = function(user) {
      return $http.post('/api/register', user);
    }

    var login = function(user) {
      return $http.post('/api/login', user);
    }

    var saveToken = function(token) {
      $localStorage.fitforlife_token = token;
    };

    var getToken = function() {
      return $localStorage.fitforlife_token;
    };

    var isLoggedIn = function() {
      var token = getToken();
      var payload;
      if (token) {
        payload = token.split('.')[1];
        payload = $window.atob(payload);
        payload = JSON.parse(payload);
        return payload.exp > Date.now() / 1000;
      } else {
        return false;
      }
    };

    var currentUser = function() {
      if (isLoggedIn()) {
        var token = getToken();
        var payload = token.split('.')[1];
        payload = $window.atob(payload);
        payload = JSON.parse(payload);
        return {
          _email: payload._email,
          _name: payload._name
        };
      }
    };

    logout = function() {
      $localStorage.fitforlife_token = null;
    };


    return {
      register: register,
      login: login,
      saveToken: saveToken,
      getToken: getToken,
      isLoggedIn: isLoggedIn,
      currentUser: currentUser,
      logout: logout
    };
  }
})();

(function() {
  angular
    .module('fitforlife')
    .controller('loginCtrl', loginCtrl);

  loginCtrl.$inject = ['$scope', '$location', '$localStorage', '$timeout', 'authService', 'Notification'];

  function loginCtrl($scope, $location, $localStorage, $timeout, authService, notify) {
    var vm = this;
    vm.user = {
      _email: '',
      _password: '',
    }


    $('#login').validate({
      rules: {
        email: {
          required: true,
          email: true,
          maxlength: 100
        },
        password: {
          required: true,
          minlength: 8,
          maxlength: 25
        }
      }
    });


    vm.loading = false;
    vm.login = function() {
      if (!$('#login').valid()) return;
      vm.loading = true;
      authService.login(vm.user)
        .then(function(response) {
          notify.success({
            message: response.data.message,
            title: response.statusText
          });
          authService.saveToken(response.data.token);
          $timeout(function() {
            $location.path('feed');
          }, 500);
        }).catch(function(err) {
          notify.error({
            message: err.data.message,
            title: err.statusText
          });
        }).finally(function() {
          vm.loading = false;
        });
    }
  }
})();

(function() {
  angular
    .module('fitforlife')
    .controller('registerCtrl', registerCtrl);

  registerCtrl.$inject = ['$scope', '$location', '$uibModal', 'authService', 'Notification'];

  function registerCtrl($scope, $location, $uibModal, authService, notify) {
    var vm = this;
    vm.user = {
      _name: '',
      _email: '',
      _password: '',
      _confirm_password: ''
    }


    $('#registration').validate({
      rules: {
        email: {
          required: true,
          email: true,
          maxlength: 100
        },
        name: {
          required: true,
          minlength: 2,
          maxlength: 40
        },

        password: {
          required: true,
          minlength: 8,
          maxlength: 25
        },
        confirm_password: {
          required: true,
          minlength: 8,
          maxlength: 25,
          equalTo: "#password"
        }
      }
    });


    vm.loading = false;
    vm.register = function() {
      if (!$('#registration').valid()) return;
      vm.loading = true;
      authService.register(vm.user)
        .then(function(response) {
          console.log(response);
          notify.success({
            message: response.data.message,
            title: response.statusText
          });

          var modalInstance = $uibModal.open({
            animation: true,
            backdrop: 'static',
            keyboard: false,
            templateUrl: '/views/_prompts/dialog.view.html',
            controller: 'dialogCtrl as vmd',
            size: 'sm',
            resolve: {
              info: function() {
                return {
                  _title: 'Awesome...',
                  _message: 'You registered successfully. Please login to get access.',
                  _confirm_label : "Let's Start!"
                };
              }
            }
          });
          modalInstance.result.then(function(data) {
            $location.path('login');
          }, function() {

          });
        }).catch(function(err) {
          console.log(err);
          notify.error({
            message: err.data.message,
            title: err.statusText
          });
        }).finally(function() {
          vm.loading = false;
        });
    }
  }
})();

